<template>
    <div class="table">
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <!-- <el-form-item label="vin号" :inline="true">
                    <el-input v-model.trim="filters.vin_number" placeholder="请输入vin号"></el-input>
                </el-form-item>
                <el-form-item label="车主名称" :inline="true">
                    <el-input v-model.trim="filters.bus_user_name" placeholder="请输入车主名称"></el-input>
                </el-form-item>
                <el-form-item label="工单阶段">
                    <el-select v-model="filters.work_status" placeholder="请选择">
                        <el-option
                            label="收费阶段"
                            value="1">
                        </el-option>
                        <el-option
                            label="付款阶段"
                            value="2">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="工单状态">
                    <el-select v-model="filters.stage_id" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button @click="search">查询</el-button> -->
                <el-button type="primary" icon="plus" @click="chooseCar">新建</el-button>
                <!-- <el-button type="primary" icon="plus">导出</el-button> -->
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="plate_number" label="车牌号" width="150">
                </el-table-column>
                <el-table-column prop="vin_number" label="vin号" width="120">
                </el-table-column>
                <el-table-column prop="band_name" label="车主名称" width="120">
                </el-table-column>
                <el-table-column prop="car_brand" label="车辆品牌" width="120">
                </el-table-column>
                <el-table-column prop="car_series" label="车辆系列" width="120">
                </el-table-column>
                <el-table-column prop="stage_name" label="工单阶段" width="120">
                </el-table-column>
                <el-table-column prop="work_status_name" label="工单状态" width="120">
                </el-table-column>
                <el-table-column prop="operater" label="当前处理人" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间">
                    <template slot-scope="{row}">
                        {{row.create_time | time}}
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button
                            type="text"
                            icon="el-icon-view"
                            @click="$router.push({path: '/insuranceServices/view', query: { id: row.id }})">
                            查看
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/insuranceServices/edit', query: { id: row.id}})"
                            v-if="isShowEdit(row)">
                            修改
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/insuranceServices/edit', query: { id: row.id, work_status: row.work_status, stage_id: row.stage_id}})"
                            v-if="isShowAudit(row)">
                            审核
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            @click="handleDelete(row, $index)"
                            v-if="isShowDel(row)">
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination layout="prev, pager, next, jumper" @current-change="handleCurrentChange" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
        
        <!-- 选择车辆 -->
        <el-dialog title="选择车辆" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>
    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import selectCar from '@/components/selectCar'
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                total: 0,
                loading: true,
                hackReset: false,
                dialogSelectCarType: false,
                selectCarOptions:{
                    tableConfig:{
                        plate_number: true,
                        vin_number: true,
                        band_name: true,
                        car_info: true
                    },
                    formConfig: {
                        val: false
                    },
                    addCar: true,
                    pageType: 'insuranceOrder'
                    
                },
                filters: {
                    vin_number:'',
                    bus_user_name:'',
                    work_status:'',
                    stage_id:'',
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }],
                idx: -1
            }
        },
        components:{
            selectCar
        },
        created() {
            this.getData();
        },
        methods: {
            isShowEdit(row){
                if(row.stage_id==0 || row.stage_id==7){
                    return row.work_status==1 && row.handle==1
                }
            },
            isShowAudit(row){
                if(row.stage_id==0 || row.stage_id==7){
                    return row.work_status!=1 && row.auth_user==1
                }else{
                    return row.auth_user==1&&row.stage_id!=15
                }
            },
            isShowDel(row){
                if(row.stage_id==0 || row.stage_id==7){
                    return row.work_status==1
                }
            },
            // 选择车辆
            chooseCar(){
                this.dialogSelectCarType = true
            },
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData(filters) {
                let params= {
                    page: this.page,
                    ...this.filters
                }
                this.loading=true
                API.apiInsuranceList(params).then(res => {
                    this.loading=false
                    if(res.code == 0){
                        this.tableData = res.data.data;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            search() {
                this.getData(this.filters)
            },
            handleDelete(row, index) {
                API.apiInsuranceDel({id: row.id}).then(res => {
                    if(res.code == 0){
                        this.tableData.splice(index, 1);
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleConfirmCar(row){
                this.dialogSelectCarType = false
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                let params = {
                    bus_id: row.id
                }
                API.apiInsuranceAdd(params).then(res => {
                    if(res.code == 0){
                        this.dialogSelectCarType = false;
                        this.getData();
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            }
        }
    }

</script>

